.render{
  // display: flex;
  // flex-direction: column;
  width: 100%;
  .navbar{
    max-width: 100%;
    margin:0 auto;
    display: flex;
    align-items: center;
    box-shadow: 0px 0 25px #d1d0d0;
    .logo{
      font-size: 18px;
      color: rgb(63, 119, 241);
      font-family: sans-serif;
      padding: 0 20px;
      font-weight: 600;
     
    }
    .logo:hover{
      color: rgb(243, 13, 52);
    }
    .admin{
      display: flex;
      align-items: center;
      margin-left: auto;
      h5{
        color: black;
        padding: 5px;
      }
      img{
        width: 20px;
        height: 25px;
      }
    }
    .Logout{
      display: flex;
      align-items: center;
      margin: 4px 25px 4px 40px;
      h5{
        color: rgb(18, 65, 221);
        padding: 5px;
      }
      h5:hover{
        color: red;
        font-size: 23px;
      }
      img{
        width: 20px;
        height: 25px;
      }
    }
  }
}
